<template>
	<div class="container"> 
  		<div class="jumbotron jumbo">
		<h2 class="text-info">Welcome to the portfolio pages</h2>
		<p>G'day, I am a prograssive front end developer who make something attractive,
		reusable, accessible user interfaces desinged to provide the best possible user exprerience</p></p>
		This page showcases some of my recent work, including a varity of prograssive web application examples
		</p>
	</div>
	<app-pagination></app-pagination>
  	 <transition id="appRender" name="pSlide" mode="out-in">
  		<router-view></router-view>
  	</transition>
  	</div>
</template>

<style scoped>

	.jumbo{
		margin: 3rem 0 2rem 0;
	}

	/*SLIDING CLASSES*/
	.pSlide-enter-active{
		animation: pSlide-in 200ms ease-out forwards;
	}
	.pSlide-leave-active{
		animation: pSlide-out 200ms ease-out forwards;
	}
	@keyframes pSlide-in{
		from{
			transform: translateX(-30px);
			opacity: 0;
		}
		to{
			transform: translateX(0);
			opacity: 1;
		}
	}
	@keyframes pSlide-out{
		from{
			transform: translateX(0);
			opacity: 1;
		}
		to{
			transform: translateX(-30px);
			opacity: 0;
		}
	}
</style>

<script>
	import Pagination from './Pagination.vue';
	export default{
		components: {
			appPagination: Pagination
		}
	}
</script>




