<template>
	<div class="container">
	<div class="row justify-content-center">
		<div class="col-lg-6">
		<h2 class="text-info">Filter with item</h2>
		<form>
			<label class="radio-inline">
			<input type="radio" v-model="selectedItem" value="All" > All </label>
			<label class="radio-inline">
			<input type="radio" v-model="selectedItem" value="Fruits" > Fruits </label>
			<label class="radio-inline">
			<input type="radio" v-model="selectedItem" value="Flowers" > Flowers </label>
		</form><br>
		</div>
		</div>
		
		
		<div class="row justify-content-center">
		<div class="col-lg-6">
		<form>
			<transition-group class="list-group" name="list" tag="ul">
				<li 
				class="list-group-item" 
				v-bind:key="item" 
				v-for="item in filteredItem">
					{{ item.name }}
				</li>
			</transition-group>
		</form>
		</div>
	</div><br><br>
	</div>
</template>

<style scoped>

.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>

<script>
	export default{
		data: function(){
			return {
				selectedItem: 'All',
				items: [
					{ name: 'Apple', category: 'Fruits' },
					{ name: 'Orange', category: 'Fruits' },
					{ name: 'Aconitum', category: 'Flowers' },
					{ name: 'Banana', category: 'Fruits' },
					{ name: 'Agapanthus', category: 'Flowers' },
					{ name: 'Grape', category: 'Fruits' },
					{ name: 'Alstroemeria', category: 'Flowers' }
				]
			}
		},
		computed: {
			filteredItem(){
				var selectCat = this.selectedItem;
				if(selectCat === "All"){
					return this.items;
				}else{
					return this.items.filter((item) => {
						return item.category === selectCat;
					});
				}
			}
		}
	}
</script>




