<template>
	<div class="container">
		<app-newquote @quoteAdded="addNewQuote"></app-newquote>
		<app-quotegrid :quotes="quotes" @quoteDeleted="quoteDelete"></app-quotegrid>
	</div>
</template>



<script>

	import Newquote from './Newquote.vue';
	import Quotegrid from './Quotegrid.vue';
	export default{
		data: function(){
			return {
				quotes: [
					{ body: 'Daily articles published by expert developers from around the world. Articles include tutorials on really innovative solutions to cool topics (e.g. object detection algorithms for mobile, social network data mining for predictive analysis, and using machine learning to predict gas prices, to name a few), in-depth analyses of new technologies, explorations of emerging technologies and recent tech trends, tech lifestyle posts (especially about remote working and digital nomadism), and more. ',
					  title: 'General Programming Blog' 
					}

				]
			}
		},
		components: {
			appQuotegrid: Quotegrid,
			appNewquote: Newquote

		},
		methods: {
			addNewQuote(quote){
				this.quotes.push(quote);
			},
			quoteDelete(index){
				this.quotes.splice(index, 1);
			}
		}
	}
</script>


