<template>
	<div class="row justify-content-center">
	<div class="col-8">

		<div class="media" v-for="(quote, index) in quotes">
		  <img class="d-flex align-self-start mr-3" src="../../assets/intelligent.png">
		  <div class="media-body">
		  <span class="close" @click="deleteQuote(index)">&times;</span>
		    <h5 class="mt-0">{{ quote.title }} </h5>
		    <p>
		    	{{ quote.body }}
		    </p>
		  </div>
		</div>

	</div>
	</div>
</template>
<style scoped>
	.media{
		border: 1px solid #000;
		padding: 2rem;
		margin-bottom: 2rem;
	}
	.close{
		cursor: pointer;
		font-size: 35px;
		color: red;
	}
</style>
<script>

	export default{
		props: ['quotes'],

		methods: {
			deleteQuote(index){
				this.$emit('quoteDeleted', index);
			}
		}
	}
</script>



