<template>
	<div class="col-4 form-area">
		<div class="row form-head">
			<div class="col-6">
				<img id="form-img" src="../../assets/logo.png" style="width: 220px;">
			</div>
		</div>
		<form @submit.prevent="routerChange">
			  <div class="form-group">
				    <label for="user">User Name</label>
				    <input type="text" class="form-control" id="user" placeholder="user name">
			  </div>
			  <div class="form-group">
				    <label for="email">Email</label>
				    <input type="email" class="form-control" id="email" placeholder="your email">
			  </div>
			  <div class="form-group">
				    <label for="pass">Password</label>
				    <input type="password" class="form-control" id="pass" placeholder="create password">
			  </div>
			   <div class="form-group">
				    <label for="pass">Confirm Password</label>
				    <input type="password" class="form-control" id="pass" placeholder="confirm password">
			  </div>
		 	  <button 
		 	  type="submit" 
		 	  class="btn btn-primary btn-block"
		 	  @submit.prevent="routerChange"
		 	  >Sign Up</button>
		</form>
	</div>
</template>

<script>
	export default{
		methods: {
			routerChange(){
				this.$router.push('/signin');
			}
		}
	}
</script>

<style scoped>
#form-img{
  -webkit-filter: sepia(1);
  filter: sepia(1);
  max-width: 100%;
  width: 150px;
}
.form-head{
	padding-bottom: 1rem;
	margin-bottom: 1rem;
	border-bottom: 4px solid #878;
	border-left: 4px solid #878;

}
</style>


