<template>
	<div class="col-4 form-area">
	<div class="row form-head">
			<div class="col-6">
				<img id="form-img" src="../../assets/logo.png" style="width: 155px;">
			</div>
			<div class="col-6">
				<router-link
					tag="a"
					to="/signin/signup"
					class="btn btn-link"
					>	
					Create new account
				</router-link>
			</div>
		</div>
		<form @submit.prevent="gotoHome">
			  <div class="form-group">
				    <label for="userName">User Name</label>
				    <input type="email" class="form-control" id="userName" placeholder="user name">
			  </div>
		  <div class="form-group">
			    <label for="passwrd">Password</label>
			    <input type="password" class="form-control" id="passwrd" placeholder="password">
		  </div>
		  	<a href="#" class="btn btn-link">Forgot password</a>
		 	 <button type="submit" class="btn btn-primary pull-right"
		 	 @submit.prevent="gotoHome">Sign In</button>
		</form>
	</div>
</template>

<script>
	export default{
		methods: {
			gotoHome(){
				this.$router.push('/');
			}
		}
	}
</script>
<style>
#form-img{
  -webkit-filter: sepia(1);
  filter: sepia(1);
  max-width: 100%;
}
.form-head{
	padding-bottom: 1rem;
	margin-bottom: 1rem;
	border-bottom: 4px solid #878;
	border-left: 4px solid #878;

}
</style>



