
<template>
<form>
<div class="row justify-content-center">
	<div class="col-lg-6">
		<h2 class="text-info">File a complaint</h2>
		<div class="form-group">
			<label for="email">Mail</label>
			<input
				type="text"
				id="email"
				class="form-control"
				v-model="userData.email"
			>
		</div>

		<div class="form-group">
			<label for="password">Password</label>
			<input
				type="password"
				id="password"
				class="form-control"
				v-model="userData.password"
			>
		</div>

		<div class="form-group">
			<label for="age">Age</label>
			<input
				type="number"
				id="age"
				class="form-control"
				v-model="userData.age"
			>
		</div>
	</div>
</div>
<div class="row justify-content-center">
	<div class="col-lg-6 form-group">
		<label for="message">Message</label><br>
		<textarea
			id="message"
			rows="5"
			v-model="message"
			class="form-control">
		</textarea>
	</div>
</div>

<div class="row justify-content-center">
	<div class="col-lg-6">
		<div class="form-group">
			<label for="sendmail">
				<input
					type="checkbox"
					id="sendmail"
					v-model="SendMail"
					value="SendMail"> Send Mail
			</label>
			<label for="sendinfomail">
				<input 
				type="checkbox"
				id="sendinfomail"
				v-model="SendMail"
				value="SendInfoMail"> Send Infomail
			</label>
		</div>
	</div>
</div>

	<div class="row justify-content-center">
	<div class="col-lg-6">
		<div class="form-group">
			<label for="male">
				<input
					type="radio"
					id="male"
					v-model="gender"
					value="Male"> Male
			</label>
			<label for="female">
				<input 
				type="radio"
				id="female"
				v-model="gender"
				value="Female"> Female
			</label>
		</div>
	</div>
</div>

<div class="row justify-content-center">
	<div class="col-lg-6">
		<label for="priority">Priority</label>
		<select
			id="priority"
			class="form-control"
			v-model="selectedPriority">

			<option v-for="priority in priorities" 
			:selected="priority == 'Medium' "> 
			{{ priority }}
			</option>

		</select>
	</div>
</div>

<div class="row justify-content-center">
<div class="col-lg-6">
<br>
	<button class="btn btn-info form-control" @click.prevent="">
	Submit !
	</button>
</div>
</div>

<div class="row justify-content-center">
<div class="col-lg-6">
<br>
	<div class="card border-dark mb-3">
		<div class="card-header text-success"> <h3>Your Data</h3> </div>

		<div class="card-body text-dark">
			<p> Mail: {{ userData.email }}</p>
			<p> Password: {{ userData.password }}</p>
			<p> Age: {{ userData.age }}</p>
			<p style="white-space: pre"> Message: {{message}}</p>
			<p><strong>Send Mail?</strong></p>
			<ul>
				<li v-for="item in SendMail">{{ item }}</li>
			</ul>
			<p> Gender: {{ gender }}</p>
			<p> Priority: {{ selectedPriority }}</p>
		</div>
	</div>
</div>
</div><br><br>
</form>
</template>

<style scoped>

</style>
<script>
export default{
data: function(){
return {
	userData: {
		email:    '',
		password: '',
		age:      23
	},
	message: 'A message here',
	SendMail : [],
	gender: 'Male',
	selectedPriority: 'High',
	priorities: ['High', 'Medium', 'Low']
}
}
}
</script>



