
<template>
	<div class="col-4">
		<div class="card border-success mb-3">
			 <div class="card-header">
			 		{{ stock.name }}
			 		<small>(Price: {{ stock.price | currency }})</small>
			 </div>
			 <div class="card-body">
			 	<div class="pull-left">
			 		<input 
			 			type="number"
			 			class="form-control"
			 			placeholder="Quantity"
			 			v-model="quantity"
			 			>
			 	</div>
			 	<div class="pull-right">
			 		<button
			 			class ="btn btn-success"
			 			@click="buyStock"
			 			:disabled="quantity <= 0 || isNaN(quantity)" >
						Buy</button>
			 	</div>
			 </div>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['stock'],
		data: function(){
			return {
				quantity: 0
			}
		}, 
		methods: {
			buyStock(){
				const order = {
					stockId: this.stock.id,
					stockPrice: this.stock.price,
					quantity: this.quantity
				};
				console.log(order);
				this.quantity = 0;
			}
		}
	}
</script>


