<template>
	<div class="row justify-content-center">
		<div class="col-lg-6">
			<h2 class="text-info">My To Do List</h2>
			<div class="input-group">
				<input class="form-control" type="text" placeholder="Add Item and hit Enter" v-model="newText" @keyup.enter="addNew">
				 <span class="input-group-btn">
			        <button class="btn btn-info" type="button" @click="addNew">Add New Item</button>
			      </span>

			</div><br>

			<div class="form-group">
				<transition-group name="list" tag="ul" class="list-group">
				<li 
				class="list-group-item "
				v-for="(item, index) in items"
				v-bind:key = "item.id"
				v-bind:value = "item.title"

				 >

					{{ item.title }} 
					<i class="fa fa-times close" @click="removeItem(index)" aria-hidden="true"></i>
				<!-- <span class="close"
				@click="removeItem(index)"> &times;</span> -->
				</li>
			</transition-group>
			</div><br><br>
		</div>
	</div>
</template>

<style scoped>
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>

<script>
	export default{
		data: function(){
			return {
				newText: '',
				items: [

					{
						id: 1,
						title: 'Do the dishes'
					},
					{
						id: 2,
						title: 'Take out the trash'
					},
					{
						id: 3,
						title: 'Mow the lawn'
					}


				],
				nextItemId: 4
			}
		},
		methods: {
			addNew(){
				this.items.push({
					id: this.nextItemId++,
					title: this.newText
				});
				this.newText = ''
			},
			removeItem(index){
				this.items.splice(index, 1);
			}
		}
	}
</script>


