<template>
		<form class="blog-form">
		<div class="row justify-content-center">
			<div class="col-8 form-group">
				<label class="text-info"><h2>Write Something Here</h2></label>
				<input type="text" class="form-control" placeholder="your title here" v-model="quotes.title">
			</div>
		</div>
		<div class="row justify-content-center">
			<div class="col-8 form-group">
				<textarea class="form-control" rows="5" 
				v-model="quotes.body" @keyup.enter="addNew" placeholder="write something here"> </textarea>
			</div>
		</div>
		<div class="row justify-content-center">
			<div class="col-8 form-group">
				<button class="btn btn-primary spacial" @click.prevent="addNew">PUBLISH YOUR ARTICLE</button>
			</div>
		</div>
		</form>
</template>

<style scoped>
	.blog-form{
		padding-top: 4rem;
	}
	.spacial{
		float: right;
		padding: 10px 50px;
		letter-spacing: 3px;
	}
</style>

<script>
	export default{
		data: function(){
			return {
				quotes: {
					body: '',
					title: ''
				}
			}
		},
		methods: {
			addNew(quote){
				this.$emit('quoteAdded', this.quotes);
				this.quotes = '';
			}
		}
	}
</script>


