import Home from './components/Home.vue';
import Portfolio from './components/portfolio/Portfolio.vue';
import Blogs from './components/blogs/Blogs.vue';
import Signin from './components/others/Signin.vue';
import Stocks from './components/stocks/Stocks.vue';

 // PORTFOLIO LINKS
import Listfilter from './components/portfolio/Listfilter.vue';
import handlingUserInput from './components/portfolio/handlingUserInput.vue';
import Arlist from './components/portfolio/Arlist.vue';
import Radiofilter from './components/portfolio/Radiofilter.vue';

// SIGN IN
import Signingin from './components/others/signingin.vue';
import Signup from './components/others/signup.vue';

export const routes = [
	{ path: '/', component: Home },
	{ path: '/blogs', component: Blogs },
	{ path: '/stocks', component: Stocks },

	{ path: '/portfolio', component: Portfolio, children: [
		{ path: '', component: handlingUserInput },
		{ path: 'handlinguserinput', component: handlingUserInput },
		{ path: 'listfilter', component: Listfilter },
		{ path: 'arlist', component: Arlist },
		{ path: 'radiofilter', component: Radiofilter }
	] },

	{ path: '/signin', component: Signin, children: [
		{ path: '', component: Signingin },
		{ path: 'signingin', component: Signingin },
		{ path: 'signup', component: Signup}
	] }
];


