<template>
	<div class="container">
	<div class="row justify-content-center">
		<div class="col-lg-6 form-group">
			<h2 class="text-info">Filter from the list items</h2>
			<input type="text" class="form-control" v-model="userInput" placeholder="search..">
		</div>
		</div>
		<div class="row justify-content-center">
		<div class="col-lg-6 form-group">
			 <transition-group class="list-group" name="list" tag="ul">
				<li 
				class="list-group-item" 
				v-bind:key="singer"
				v-for="singer in singerFilter"> 
				{{ singer }} 
				</li>

			</transition-group>
		</div>
	</div><br><br>
	</div>
</template>

<style scoped>

.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>

<script>
	export default{
		data: function(){
			return {
				singers: ['Adele', 'Angelina Joli', 'Anisa joew', 'Taylor Swift', 'Lady Gaga', 

							'Priyanka Chupra', 'Tove lo', 'Ellie Goulding'	],
				userInput: ''
			}
		},
		computed: {
			singerFilter(){
				var self = this;
				return this.singers.filter(function(elem){
					return elem.toLowerCase().indexOf(self.userInput.toLowerCase()) >= 0;
				})
			}
		}
	}
</script>


